/* Inner flex container */

/* The container <div> - needed to position the dropdown content */
.Dropdown_dropdown__CvICm {
	position: relative;
	display: inline-block;
}

/* Dropdown Content (Hidden by Default) */
.Dropdown_dropdownContent__5t1pg {
	color: #fff;
	display: none;
	position: absolute;
	background-color: #000;
	min-width: 160px;
	box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
	z-index: 1;
	right: 0;
}

/* Links inside the dropdown */
.Dropdown_dropdownContent__5t1pg a {
	color: #fff;
	padding: 12px;
	text-decoration: none;
	display: block;
}

/* Change color of dropdown links on hover */
.Dropdown_dropdownContent__5t1pg a:hover {
	background-color: #333;
}

/* Show the dropdown menu on hover */
.Dropdown_dropdown__CvICm:hover .Dropdown_dropdownContent__5t1pg {
	display: block;
}

/* Change the background color of the dropdown button when the dropdown content is shown */
.Dropdown_dropdown__CvICm:hover .Dropdown_dropbtn__DBJCv {
	background-color: #333333;
